iframe {
  display: none;
}

.App {
  background-color: whitesmoke;
}

.Person {
  background-color: blueviolet;
  width: fit-content;
  margin-bottom: 16px;
  padding: 8px;
  border: 1px solid #000;
  border-radius: 8px;

  &__name {
    margin-top: 8px;
  }

  &__age {
    color: rgb(245, 18, 48);
    font-weight: bold;
  }

  &:hover {
    background-color: yellowgreen;
  }
}
